@import "~variable";
@import "~mixins";

.download-progress-modal {
  width: 600px;
  // height: 500px;
  h4 {
    margin-bottom: 10px;
    text-align: center;
    font-weight: 400;
  }
  .bg_box {
    position: relative;
    width: 410px;
    height: 19px;
    margin: 0 auto;
    // border: 1px solid red;
    border-radius: 19px;
    overflow: hidden;
    background-color: #EAEEF5;
  }
  /*  */
  .bg_default {
    /* 1s */
    animation-duration: 10s;
    /**/
    /* animation-fill-mode: forwards;  */
    /*   */
    // animation-iteration-count: infinite;
    /*  */
    animation-name: animationa;
    /*  */
    animation-timing-function: linear;
    /*  */
    /* background: linear-gradient(to right, #fff 0%, #fff 50%, transparent 50%, transparent 100%); */
    background: #5093E1;
    /* :  */
    width: 100%;
    height: 15px;
    border-radius: 15px;
    position: absolute;
    top: 2px;
  }

  @keyframes animationa {
      /*  */
      0% {
          /* background-position: -410px 0; */
          left: -410px;
      }
      100% {
          /* background-position: 410px 0 */
          left: 0px;
      }
  }
}



